import * as options from './RouteTypes';

const initState = {
  route:{
    ID: "",
    routeName: "",
    schedule:{
      startTime: "",
      endTime: "",
      interval: ""
    },
    price:"", 
    destination:"",
    aditionalInfo: {
      transportType: "",
      details: "",
      warnings: ""
    },
    position: [] 
  }
}




const RouteReducer = (state = initState, action) =>{
  switch (action.type) {
    case options.UPDATE_POSITION:
      const { lat:updated_lat,lng:updated_lng,
          currentPosition:{lat:current_lat,lng:current_lng}} = action.refData;
      
      return {
        route:{
          ...state.route,
          position:state.route.position.map((coords,index)=>{  
            if(current_lat === coords.LatLng[0]){
              if(current_lng === coords.LatLng[1]){
                return {
                      ...coords,
                      LatLng:[updated_lat,updated_lng]
                    }
              }else{
                return coords
              }   
            }else{
              return coords
            }
          })
        }
      }
    case options.UNLOAD_ROUTE:
      return{
        route: initState.route
      }
    case options.FILL_ROUTE:
      const route = action.route
      return {
        route:route
      }

    case options.CHANGE_ROUTE_TRANSPORT_TYPE:
      const transportType = action.transportType;
      return{
        ...state,
        route:{
          ...state.route,
          aditionalInfo:{
            ...state.route.aditionalInfo,
            transportType:transportType
          }
        }
      }
    case options.ADD_POSITION:
      const { lat,lng, street_name } = action.routePositionInfo;
      return{
        route:{   
          ...state.route,
          position:[
            ...state.route.position,
            {
              LatLng:[lat,lng],
              streetName:street_name
            }
          ]
        }
      }
    case options.DELETE_POSITION:
      let coordinatesToDelete = action.event.target.elements[0].value;
      return{
        route:{
          ...state.route,
          position:[ 
            ...state.route.position.filter(function(coords,index){
              // eslint-disable-next-line
              if(coordinatesToDelete == coords.LatLng[0]){
                return !coords
              }else{
                return coords
              }
            })
          ]
        }
        
      }
    case options.ROUTE_CHANGE:
      const { event } = action;
      
      const route_name = event.target.name;
      const input_value = event.target.value;
      console.log(state.route)
      
      switch (route_name) {
        case 'ID':
          return {
            route:{
              ...state.route,
              ID:input_value
            }
          }
        case 'routeName':
          return{
            route:{
              ...state.route,
              routeName:input_value
            }
          }
        case 'price':
          return{
            route:{
              ...state.route,
              price:input_value
            }
          }
        case 'destination':
          return{
            route:{
              ...state.route,
              destination:input_value
            }
          }
        case 'startTime':
          return{
            route:{
              ...state.route,
              schedule:{
                ...state.route.schedule,
                startTime:input_value
              }
            }
          }
          case 'endTime':
          return{
            route:{
              ...state.route,
              schedule:{
                ...state.route.schedule,
                endTime:input_value
              }
            }
          }
          case 'interval':
          return{
            route:{
              ...state.route,
              schedule:{
                ...state.route.schedule,
                interval:input_value
              }
            }
          }
          case 'details':
          return{
            route:{
              ...state.route,
              aditionalInfo:{
                ...state.route.aditionalInfo,
                details:input_value
              }
            }
          }
          case 'warnings':
          return{
            route:{
              ...state.route,
              aditionalInfo:{
                ...state.route.aditionalInfo,
                warnings:input_value
              }
            }
          }
        default:
          return state.route;
      }/*
      return{
        ...state,
        route:
          {
            ...state.route,
            [route_name]:input_value,
            schedule: {
              ...state.route.schedule,
              [route_name]:input_value
            },
            aditionalInfo:{
              ...state.route.aditionalInfo,
              [route_name]:input_value
            }
          }
      }*/
    default:
      return state;
  }
  
}

export {RouteReducer};