
import React from 'react';

import bus from '../../../../../Images/directions_bus-24px.svg';
import Edit from '../../../../../Images/edit-24px.svg';
import Delete from '../../../../../Images/delete-24px.svg';

import { connect } from 'react-redux';
import { MouseIn , MouseOut, toggleConfirmationMessage } from '../../../../../Redux/GeneralReducer/index'

import { loadApp } from '../../../../../Redux/CombinedActions'
import { deleteRoute } from '../../../../../Redux/AsyncActions/index'

import {RouteUpdateButton,RouteDeleteButton,
        BottomDiv,MiddleDiv,TopDiv,DetailContainer} from './styled'
 
function RouteDetailContainer (props) {
    const { routeId,routeName,routeLocation,routeTime,routeSchedule,routePrice,switchConfirmationMessage} = props;

    const { onMouseHover,onMouseOut,deleteRoute,fillingApp,states:{GeneralReducer:{confirmationMessage}} } = props;
    const { outedRoutes: currentRoute } = props;
    return (
            <DetailContainer 
                onClick={(event)=>{
                    event.stopPropagation();
                    fillingApp(currentRoute)
                }}
    
                onPointerEnter={(event)=>{
                    event.stopPropagation();
                    onMouseHover(currentRoute);
                }}
                onPointerLeave={onMouseOut}
            >
                    
                <TopDiv>
                    <div className="route_icon">   
                        <a href="#"><img src={bus} alt=""/></a>
                    </div>
                    <div className="route_id">{routeId}</div>
                    <div className="route_name">{routeName}</div>
                    <div className="route_location">{routeLocation}</div>
                </TopDiv>

                <MiddleDiv>
                    <div className="route_time">{routeTime}</div>
                </MiddleDiv>

                <BottomDiv>
                    <div className="route_schedule">{routeSchedule}</div>
                    <div className="route_price">${routePrice}</div>
                </BottomDiv>

                <RouteDeleteButton 
                    className="font-bold rounded "
                    value={routeId}
                    onClick={(event)=>{
                        event.stopPropagation();
                        switchConfirmationMessage({confirmationMessage,routeId});
                        //deleteRoute(routeId);
                    }}
                >
                    <img src={Delete} alt=""/>
                </RouteDeleteButton>

                <RouteUpdateButton 
                    className="font-bold rounded "    
                    type="button"
                    onClick={(event)=>{
                        fillingApp(currentRoute);
                    }}
                >
                    <img src={Edit} alt=""/>
                </RouteUpdateButton>
            </DetailContainer>
    )
}


const mapRootStateToProps = state=>{
    return {
        states:state
    }
}

const mapDispatchToProps = (dispatch)=>{
    return{
        onMouseHover: (route)=>{dispatch(MouseIn(route))},
        onMouseOut: ()=>{dispatch(MouseOut())},
        deleteRoute: routeId=>{dispatch(deleteRoute(routeId))},
        fillingApp: (route)=>{dispatch(loadApp(route))},
        switchConfirmationMessage: (payload)=>{dispatch(toggleConfirmationMessage(payload))}
    }
}



export default connect(mapRootStateToProps,mapDispatchToProps)(RouteDetailContainer)