import React,{Fragment} from 'react'
import CustomLatLong from './Components/LatLongPairs/LatLongPairs';

import {RouteContainer,InputLabelContainer,Arrow} from './styled';
import { connect } from 'react-redux'

import { deleteMarkerPolylineRoutePosition } from '../../../../Redux/CombinedActions'
import { markerReferenceMouseIn,markerReferenceMouseOut, animateMarker, deAnimateMarker } from '../../../../Redux/ReduxActions'

import { v4 as uuidv4 } from 'uuid';

function EditableRoutes (props) {
        const {deleteMPRouteP,onHover,MouseLeave,Hover,mouseLeave} = props;
        const {route} = props.states.RouteReducer;
    return (
        <Fragment>
            <RouteContainer className="coordinatesContainer" 
            >
            {route.position.map((coords,index)=>{
                return (
                    <form 
                        key={index+2}
                        onSubmit = {(event)=>{
                            event.preventDefault();
                            deleteMPRouteP(event)
                        }}  
                    >
                        <InputLabelContainer 
                                onMouseEnter ={(event)=>{
                                    onHover(coords.LatLng)
                                    const lat = coords.LatLng[0];
                                    const lng = coords.LatLng[1];
                                    Hover({lat,lng})
                                }}
                                onMouseLeave = {(event)=>{
                                    MouseLeave();
                                    const _lat = coords.LatLng[0];
                                    const _lng = coords.LatLng[1];
                                    mouseLeave({_lat,_lng});
                                }}
                        >
                            <CustomLatLong 
                                index_received = {index}
                                coords = {coords.LatLng[0]}
                                streetName = {coords.streetName}
                            />
                        </InputLabelContainer>
                    </form>
                )
            })} 
        </RouteContainer> 
        <Arrow />
        </Fragment>
    )  
}

const MapRootStateToProps = (states)=>{
    return {
        states:states
    }
}

const MapDispatchToProps = (dispatch)=>{
    return{
        deleteMPRouteP: (event)=>{dispatch(deleteMarkerPolylineRoutePosition(event))},
        onHover: (data)=>{dispatch(markerReferenceMouseIn(data))},
        MouseLeave: ()=>{dispatch(markerReferenceMouseOut())},
        Hover:(coords)=>{dispatch(animateMarker(coords))},
        mouseLeave: (coords)=>{dispatch(deAnimateMarker(coords))}
    }
}
export default connect(MapRootStateToProps,MapDispatchToProps)(EditableRoutes)