
import { createStore,combineReducers, applyMiddleware } from 'redux';
//import logger from 'redux-logger'
import thunk from 'redux-thunk'

import {GeneralReducer,MapReducer,
        MarkersReducer,RouteReducer,
        RoutesReducer,PolylineReducer } from '../Redux/ReduxReducers';

const reducer = combineReducers({
    MapReducer:MapReducer,
    GeneralReducer:GeneralReducer,
    MarkersReducer:MarkersReducer,
    RouteReducer:RouteReducer,
    RoutesReducer:RoutesReducer,
    PolylineReducer:PolylineReducer
})

const store = createStore(reducer,applyMiddleware(thunk));

export default store