import React from 'react';
import bus from '../../../Images/bus.svg';
import car from '../../../Images/car.svg';
import minibus from '../../../Images/minibus.svg';

import {connect} from 'react-redux';
import { buttonGroupControl } from '../../../Redux/CombinedActions'
import { ButtonContainer } from './ButtonGroup_styled';



function ButtonGroup(props){
    const { handleButtonGroup } = props;
    //const { activeButtonGroup } = outedGeneralState;
    const { activeButtonGroup } = props.states.GeneralReducer.generalState;
    return(
        <ButtonContainer className="buttons-container transportType">
            <ul>
                <li className={activeButtonGroup==='bus'?"active":''}  
                onClick={(event)=>{
                    event.stopPropagation();
                    handleButtonGroup('bus');
                }}><a href="#"  >
                    <img src={bus} alt=""/> 
                </a></li>
                
                <li className={activeButtonGroup==='carro'?"active":''}
                onClick={(event)=>{
                    event.stopPropagation();
                    handleButtonGroup('carro');
                }}><a href="#">
                    <img src={car} alt="" />  
                </a></li>

                <li className={activeButtonGroup==='minibus'?"active":''} 
                onClick={(event)=>{
                    event.stopPropagation();
                    handleButtonGroup('minibus');
                }}><a href="#">
                    <img src={minibus} alt="" />
                </a></li>
            </ul>
        </ButtonContainer>
    )
}

const MapRootStateToProps = (states)=>{
    return {
        states:states
    }
}

const MapDispatchToProps = (dispatch)=>{
    return{
        handleButtonGroup:(transportType)=>{dispatch(buttonGroupControl(transportType))}
    }
}
export default connect(MapRootStateToProps,MapDispatchToProps)(ButtonGroup)