import * as options  from './MarkersTypes' 

export const addingMaker = (coords)=>{
  return{
    type: options.ADD_MARKER,
    coords:coords
  }
}
export const updatingMarker = (refData)=>{
  return{
    type:options.UPDATE_MARKER,
    refData:refData
  }
}

export const deleteMarker = (event)=>{
  return {
    type: options.DELETE_MARKER,
    event:event
  }
}

export const fillingMarker = (currentRoute)=>{
  return {
    type: options.FILL_MARKER,
    currentRoute:currentRoute
  }
}

export const unloadMarkers = ()=>{
  return {
    type:options.UNLOAD_MARKERS
  }
}


export const animateMarker = (payload)=>{
  
  return {
    type:options.MARKER_CHANGE_ANIMATION,
    payload:payload
  }
}

export const deAnimateMarker = (payload)=>{
  
  return {
    type:options.MARKER_REMOVE_ANIMATION,
    payload:payload
  }
}

