import styled from 'styled-components';

export const Custom_Message = styled.div`
    animation:pulsate-fwd repeat;
    z-index:5;
    position:relative;
    top:50%;
    font-size:2rem;
    justify-content:center;
    text-align:center;
    align-items:center;
`;

export const Container = styled.div`
    position: absolute;
    left:0;
    top: 0;
    width: 20%;
    height:100%;
    box-shadow: 2px 2px 25px black;
    //*padding:1rem;**/


    display:grid;
    grid-template-columns:20% 20% 20% 40%;
    grid-template-rows:60% 40%;
    grid-template-areas:
    'form   form  form   form'
    'form   form  form   form';

    background-color:white;


    .formInputContainer{
        grid-area:form;
        padding:3rem;
    }

    .routesContainer{
        grid-area:form;
    }


    .coordinatesContainer , .formInputContainer{
        --scrollbarBG: #CFD8DC;
        --thumbBG: #90A4AE;
    }
    .coordinatesContainer::-webkit-scrollbar, .formInputContainer::-webkit-scrollbar {
        width: 10px;
        display: none;
    }
    .coordinatesContainer, .formInputContainer {
        scrollbar-width: thin;
        scrollbar-color: var(--thumbBG) var(--scrollbarBG);
    }
    .coordinatesContainer::-webkit-scrollbar-track, .formInputContainer::-webkit-scrollbar-track {
        background: var(--scrollbarBG);
    }
    .coordinatesContainer::-webkit-scrollbar-thumb, .formInputContainer::-webkit-scrollbar-thumb {
        background-color: var(--thumbBG) ;
        border-radius: 6px;
        border: 3px solid var(--scrollbarBG);
    }

`;

export const CustomH1 = styled.h1`
    font-size: 5rem;
    color:black;
    margin-left:1rem;
`;



export const Input1 = styled.input`
    border: 1px solid black;
    border-radius: 20px;
    color: #222;
    font-size: 20px;
    padding: 0.4rem 0.4rem;
    transition: 0.25s;

    :focus {
        outline: 2px dashed #945;
        outline-offset: 3px;
    }
`;
