import React from 'react'
import RouteDetailContainer from './Components/RouteDetailContainer'

import { Container,RoutesContainer } from './styled'
import { connect } from 'react-redux';

function  Routes (props)  {
    const { routes } = props.states.RoutesReducer; 

    if (routes.length > 0 ){
        return (
            <Container>
                <RoutesContainer className="routeDetail">
                    {routes.map((route,index)=>{
                        return(
                            <RouteDetailContainer
                                outedRoutes = {route}
                                key={index+42}
                                routeId={route.ID}
                                routeName={route.routeName}
                                routeLocation={route.destination}
                                routeTime={"Frecuencia de vehiculo cada " + route.schedule.interval}
                                routeSchedule ={route.schedule.startTime + ' - '+ route.schedule.endTime}
                                routePrice = {route.price}
                            /> 
                        )
                    })}
                </RoutesContainer>
            </Container>
        )
        }else{
            return (
                <Container className="routesContainer">
                    <h3 className="routeTitle">Titulo</h3>
                    <RoutesContainer className="routeDetail">
                        Loading Data...
                    </RoutesContainer>
                </Container>
            )
        }
    }
    

const mapRootStateToProps = state=>{
    return {
        states:state
    }
}

export default connect(mapRootStateToProps)(Routes)