import React,{Fragment} from 'react';
import {CustomButton,InputContainer,CustomDeleteButton} from './LatLongPairs_styled'
import DIcon from '../../../../../../Images/delete-24px.svg'

function CustomLatLong ({index_received,coords,streetName}) {
    return (
        <Fragment>
            <InputContainer onmouseover={(event)=>{console.log(event)}}>
                <CustomButton  
                    id={index_received} 
                    name={index_received} 
                    value={coords} 
                > 
                {index_received}
                </CustomButton>
                
                <CustomDeleteButton>
                    <img src={DIcon} alt=""/> <a href="xc"></a> 
                </CustomDeleteButton>
            </InputContainer>
        </Fragment>
    )
}

export default CustomLatLong