import * as options from './RouteTypes';



export const handleInputChange = (event)=>{
  return {
      type: options.ROUTE_CHANGE,
      event:event
  }
}

export const AddingRoutePosition = (routePositionInfo)=>{
  return {
    type:options.ADD_POSITION,
    routePositionInfo:routePositionInfo
  }
}
export const updatingRoutePosition = (refData)=>{
  return {
    type: options.UPDATE_POSITION,
    refData:refData
  }
}

export const deleteRoutePosition = (event)=>{
  return {
    type:options.DELETE_POSITION,
    event:event
  }
}

export const onChangeRouteTransportType = (transportType)=>{
  return{
    type:options.CHANGE_ROUTE_TRANSPORT_TYPE,
    transportType:transportType
  }
}

export const fillRoute = (route)=>{
  return{
    type:options.FILL_ROUTE,
    route:route
  }
}

export const unloadRoute = ()=>{
  return {
    type:options.UNLOAD_ROUTE
  }
}