import * as options   from './GeneralTypes'

const initState = {
  form: {
      transportType:'bus'
  },
  //selected, hovered, normal
  routeState:'normal',
  hoveredPolylineCoordinates:[],
  hoverMarker:[],
  confirmationMessage:false,
  routeIdToDelete:'',
  generalState:{
      //post,put
      postUpdateButton:'post',
      routeHover:false,
      routeSelected:false,
      //form,route
      tabs:'form',
      //bus,carro,minibus
      activeButtonGroup:'bus'
  }
}



const GeneralReducer = (state = initState, action) =>{
  switch (action.type) {
    case options.CHANGE_CONFIRMATION_MESSAGE:
      const {confirmationMessage:value,routeId}  = action.payload;
      console.log(action.payload)
      return{
        ...state,
        routeIdToDelete:routeId,
        confirmationMessage:!value
      }
    case options.MARKER_REFERENCE_MOUSE_IN:
      const LatLng = action.coords;
      return {
        ...state,
        hoverMarker:LatLng
      }
    case options.MARKER_REFERENCE_MOUSE_OUT:
      return {
        ...state,
        hoverMarker:[]
      }
    case options.RESET_GENERAL_SETTINGS:
      return{
        form: {
          transportType:'bus'
        },
        //selected, hovered, normal
        routeState:'normal',
        hoveredPolylineCoordinates:[],
        hoveredMarkers:[],
        generalState:{
            //post,put
            postUpdateButton:'post',
            routeHover:false,
            routeSelected:false,
            //form,route
            tabs:'form',
            //bus,carro,minibus
            activeButtonGroup:'bus'
        }
      }
    case options.CHANGE_POST_TO_PUT_BUTTON:
      return{
        ...state,
        generalState:{
          ...state.generalState,
          postUpdateButton:'put'
        }
      }
    case options.CHANGE_PUT_TO_POST_BUTTON:
      return{
        ...state,
        generalState:{
          ...state.generalState,
          postUpdateButton:'post'
        }
      }
    case options.MOUSE_IN:
      const { position} = action.route;
      const newHoveredPolylineCoordinates = position.map((routeFromChild,index)=>{
        const [lat,lng] = routeFromChild.LatLng; 
          return {
            lat:lat,lng:lng 
          };
      });
      return{
        ...state,
        routeState:'hovered',
        hoveredPolylineCoordinates:newHoveredPolylineCoordinates
      }
    case options.MOUSE_OUT:
        return{
          ...state,
          routeState:'normal'
        }
    case options.FORM_TAB_CLICK:
      return {
        ...state,
        generalState:{
          ...state.generalState,
          tabs:'form'
        }
      }
    case options.ROUTE_TAB_CLICK:
      return{
        ...state,
        generalState:{
          ...state.generalState,
          tabs:'route'
        }
      }
    case options.CHANGE_TRANSPORT_TYPE:
      const transportType = action.transportType;
      return{
        ...state,
        generalState:{
          ...state.generalState,
          activeButtonGroup:transportType
        }
      }
    default:
      return state;
  }
  
}

export {GeneralReducer};
