import axios from 'axios'
const baseURL = 'https://maps.googleapis.com/maps/api/geocode/json?';
const bddHelper = {

getRequest (url) {
    return axios(
        {
            method:'get',
            url:url,
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY
            },
        }
    )
    .then(res=>{
        const result = res.data.resolve;
        return result
    })
    .catch(err=>{
        console.log(err)
    })
},

getGeocodeRequest(lat,lng){
    const url = `${baseURL}latlng=${lat},${lng}&key=${process.env.REACT_APP_GOOGLE_GEOCODE}`;
    return axios.get(url)
    .then(res=>{
        const result = res.data.results[0].formatted_address
        return result
    }).catch(err => {
        console.log(err);
    });

},

postRequest(url, data_received){
    console.log(url,data_received)
    return axios(
        {
            method:'post',
            url:url,
            data:data_received,
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY
            }
        }
    )
    .then(res=>{
        console.log(res);
    }).catch(err=>{
        console.log(err)
    })
},

putRequest(url, data_received){
    const {ID,routeName,schedule,price,destination,aditionalInfo,position
    } = data_received;

    return axios(
        {
            method:'put',
            url:`${url}/${ID}`,
            data:{
                routeName:routeName,
                schedule:schedule,
                price:price,
                destination:destination,
                aditionalInfo:aditionalInfo,
                position:position
            },
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY
            }
        }
    )
    .then(res=>{
        const result = res;
        console.log(res);
        return result
    }).catch(err=>{
        console.log(err)
    });
},

deleteRequest(url, routeID){
    return axios  (
        {
            method:'delete',
            url:`${url}/${routeID}`,
            headers:{
                'key':process.env.REACT_APP_DOBLE_VIA_KEY
            }
        }
    )
    .then(res=>{
        const result = res;
        return result
    }).catch(err=>{
        console.log(err)
    });
}

}


export default bddHelper