import styled from 'styled-components';

export const BackDrop = styled.div`
  background-color: rgba(0, 0, 0, 0.5);
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
  z-index:1;

  display:flex;
  align-content:center;
  align-items:center;
  justify-content:center;
  
  backdrop-filter: blur(0.1rem);
  
 
`;

export const ConfirmationWindow = styled.div`
  position:relative;
  bottom:10rem;

  width: 40rem;
  height: 12em;
  background-color: white;

  padding:2rem;

  border-radius: 20px;
  text-align:center;
  color:white;

  
`;


export const CustomH3 = styled.h3`
  color: #000;
  font-size:2rem;
`;

export const CustomButton = styled.button`
  color:white;
  font-weight:700;
  padding:0.5rem 1rem;
  border-radius:20px;
  transition:0.5s;

  margin:2rem;  
  :hover{
    background-color:blue;
    
  }
`;