
import React from 'react';
import {Container} from './LeftContainer_styled';

import Routes from './Components/Routes/Routes'
import EditableRoutes from './Components/EditableRoutes/EditableRoutes'
import FormRouteTabs from './FormRouteTabs/FormRouteTabs';

import { connect } from 'react-redux';
import  Form  from './Form/Form';

import { ThemeProvider } from 'styled-components'
import Modal from '../../Modal/Modal'


function LeftContainer (props)  {
    const theme = {
        generalState:props.states.GeneralReducer.generalState
    }

    const { route } = props.states.RouteReducer;
    const { generalState }  = props.states.GeneralReducer;
    
    return (
    <ThemeProvider theme={theme}>
        <Container className='Container'>
            <FormRouteTabs />
            {generalState.tabs === 'form'
                //*if
                ? <Form/>
                //*else
                : <Routes/>    
            }  

            {route.position.length > 0
                ? generalState.tabs === 'form' && <EditableRoutes />
                : <div></div> 
            }
        </Container>
    </ThemeProvider>
    )
}


const mapRootStateToProps = state=>{
    return {
        states:state
    }
}



export default connect(mapRootStateToProps) ( LeftContainer )