import { MAP_CLICK } from './MapTypes'
import mapStyles from './Files/mapStyles'

const initState = {
  zoom: 14,
  initial_center:{
    lat: 18.476229,
    lng: -69.868778
  },
  center:{
      lat: 40.854885,
      lng: -88.081807
  },
  styles:mapStyles
}


const MapReducer = (state = initState, action) =>{
  switch (action.type) {
    case MAP_CLICK:
      return state 
    default:
      return state;
  }
  
}

export  {MapReducer};