import React,{Component} from 'react';
import './App.css';
import { 
  Map,
  GoogleApiWrapper, 
  Marker, 
  Polyline
} from 'google-maps-react';

import LeftContainer from './Components/LeftContainer/LeftContainer';

import { connect } from 'react-redux';
import { getRoutes } from './Redux/AsyncActions/index'
import { OnMapClick,testing } from './Redux/MapReducer/index';

import { markerDragEnd } from './Redux/ReduxActions'
import Modal from './Modal/Modal'

class MainMap extends Component {

  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
  }

  
  componentDidMount(){
    const { fillRoute } =  this.props;
    fillRoute()
  }


  render() {
    const map = this.props.states.MapReducer;
    
    const { polylineCoordinates,options } = this.props.states.PolylineReducer;

    const { markersArray:markers, icon } = this.props.states.MarkersReducer;
    
 
    const {google,onMapClick,onDragend,testing} = this.props;

    const { hoveredPolylineCoordinates,routeState,hoverMarker } = this.props.states.GeneralReducer;
    //testing(google);
    //console.log(markers)
    return (
      <Map
        google= {google}
        center= {map.center}
        styles= {map.styles}
        className= {"map"}
        zoom={map.zoom}
        onClick = {(mapProps, map, clickEvent)=>{
          //console.log(mapProps.clickEvent)
          const { latLng } = clickEvent;
          const lat = parseFloat ( latLng.lat() );
          const lng = parseFloat ( latLng.lng() );

          onMapClick({lat, lng})
        }}
        initialCenter={map.initial_center}>

        <Modal/>
        
      {markers.map((marker, index) => (
        <Marker
          key={index}
          position={
            routeState==='normal'
            ? marker.position
            : index===0
              ? marker.position
              : index === markers.length-1 
                ? marker.position
                : []
          }
          options={{icon}} 
          title ={marker.title}
          name={'SOMA'}
          label={marker.title}
          visible={marker.visible}
          animation={google.maps.Animation[marker.animation]}
          draggable={true}
          onDragend={(markerProps, map, clickEvent)=>{
            const lat = clickEvent.latLng.lat();
            const lng = clickEvent.latLng.lng();
            const currentPosition = markerProps.position;

            onDragend({lat, lng, currentPosition})
          }}
        />
      ))}
      <Polyline 
        path = {routeState==='normal'?polylineCoordinates:hoveredPolylineCoordinates}
        options={options}
      /> 
      <LeftContainer />
     
        
    </Map>
  );
}
}

const Maps =  MainMap = GoogleApiWrapper({
  apiKey: (process.env.REACT_APP_GOOGLE_API)
})(MainMap);


const mapRootStateToProps = state=>{
  return {
    states:state
  }
}
const mapDispatchToProps = (dispatch)=>{
  return{
    fillRoute: ()=>{dispatch(getRoutes())},
    onMapClick: (mapProps)=>{dispatch(OnMapClick(mapProps))},
    onDragend: (markerProps)=>{dispatch(markerDragEnd(markerProps))},
    testing : (googleProps)=>{dispatch(testing(googleProps))}
  }
}

export default connect(mapRootStateToProps,mapDispatchToProps)(Maps) 