
import busIcon from '../../Images/directions_bus-24px.svg'
import * as options from './MarkersTypes';

const initState = {
    icon: { url: busIcon, scaledSize: { width: 28, height: 28 } },
    markersArray:[    
      {
        title: "",
        name: "",
        animation:'DROP',
        position: { },
        visible:true,
      }
    ]
}



const MarkersReducer = (state = initState, action) =>{
  switch (action.type) {
    case options.MARKER_REMOVE_ANIMATION:
      const {_lat:_lat_,_lng:_lng_} = action.payload;
      return{
        ...state,
        markersArray:state.markersArray.map((marker,index)=>{
          if(_lat_ === marker.position.lat){
            if(_lng_ === marker.position.lng){
              return{
                ...marker,
                position:{lat:_lat_,lng:_lng_},
                animation:'',
                visible:true
              }
            }else{
              return marker
            }
          }else{
            return marker
          }
        })
      }
    case options.MARKER_CHANGE_ANIMATION:
      const {lat:_lat,lng:_lng} = action.payload;
      return{
        ...state,
        markersArray:state.markersArray.map((marker,index)=>{
          if(_lat === marker.position.lat){
            if(_lng === marker.position.lng){
              return{
                position:{lat:_lat,lng:_lng},
                animation:'BOUNCE',
                visible:true
              }
            }else{
              return marker
            }
          }else{
            return marker
          }
        })
      }
    case options.UPDATE_MARKER:
      const { lat:update_lat,lng:update_lng,
          currentPosition:{lat:current_lat,lng:current_lng}} = action.refData;
      return {
        ...state,
        markersArray:state.markersArray.map((marker,index)=>{     
          if(current_lat === marker.position.lat){
            if(current_lng === marker.position.lng){
              return {
                ...marker,
                animation:'',
                position:{lat:update_lat,lng:update_lng}
              }
            }else{
              return marker
            }   
          }else{
            return marker
          }
        })
      }
    case options.UNLOAD_MARKERS:
      return{
        ...state,
        markersArray:[]
      }
    case options.FILL_MARKER:
      const  { position } = action.currentRoute;

      return {
        ...state,
        markersArray:position.map((routeFromChild,index)=>{
          const [lat,lng] = routeFromChild.LatLng; 
          return {
            visible:true,
            animation:'DROP',
            position: { lat, lng }
          };
        })
      }
    case options.ADD_MARKER:
      const { lat,lng } = action.coords;
      return{
        ...state,
        markersArray: [
          ...state.markersArray,
        {
          visible:true,
          animation:'DROP',
          position: { lat, lng }
        }]
      }
    case options.DELETE_MARKER:
      let coordinatesToDelete = action.event.target.elements[0].value;
      
      return{
        ...state,
        markersArray:[
          ...state.markersArray.filter(
            function(marker,index){
              // eslint-disable-next-line
              if(coordinatesToDelete == marker.position.lat){
                return !marker
              }else{
                return marker
              }
            }) 
        ]
      }
    default:
      return state;
  }
  
}

export {MarkersReducer};