import styled from 'styled-components';

const RouteContainer = styled.div`
    overflow:auto;
    color:white;
    display:grid;
    grid-template-columns:1fr 1fr;
    border-right: 8px outset rgba(28,110,164,0.37);
    border-radius: 0px 40px 40px 40px;
    padding:1rem;

    transition: 500ms;

    margin-top:5rem;
    max-height:20rem;
    min-height:3.5rem;
    max-width:20rem;
    background-color:white;
    z-index:5;

    //*positioning **/
    position:absolute;
    top:6.68%;
    left:106%;  
`;

const InputLabelContainer = styled.div`
    position:relative;
    background-color:white;
    animation:fadeIn 1s;
    
    transition:200ms;

    
`;

const Arrow = styled.div`
        color:white;
        z-index:5;
        position:absolute;
        top:15%;
        left:100%;  
        border-top: 25px solid transparent;
        border-right: 25px solid white;
        border-left: none;
        border-bottom: 25px solid transparent;
`;

export {RouteContainer,InputLabelContainer,Arrow}

