import styled from 'styled-components';

export const FormInputContainer = styled.form`
    animation:fadeIn 1s;
    margin-top:4rem;
    padding: 0.5rem;
    overflow:auto;

    display:grid;
    grid-template-columns:repeat(4,1fr);
    grid-template-areas: repeat(6,1fr);
    grid-gap:1rem;
    
    
/*
grid-template-areas: 
'routeID routeName routeName routeName'
'transportType transportType warning warning'
'starts ends . interval'
'price . destination destination'
'detail detail detail detail'
'. button button .';*/

grid-template-areas:
'ID             ID           ID            ID'
'routeName     routeName     routeName     routeName'
'transportType transportType transportType transportType'
'price         price         interval      interval'
'startTime     startTime     endTime       endTime'
'destination   destination   destination   destination'
'detail        detail        detail        detail'
'warnings      warnings      warnings      warnings'
'button        button        button        button';



.routeID{
    grid-area: ID;
}

.routeName{
    grid-area: routeName;
}
.transportType{
    grid-area: transportType;
    margin-bottom:2rem;
    position: relative;

    ::after{
        position:absolute;
        top:5em;
        left:0;
        content:"";
        display: block;
        width:16rem;
        border-color:gray;
        border-bottom:solid 2px;
        opacity:0.1;
        box-shadow: 5px 5px 10px #c2c0bc;
    }
}

.price{
    grid-area: price;
}

.startTime{
    grid-area: startTime;
}

.endTime{
    grid-area: endTime;
}
.interval{
    grid-area: interval;
}

.destination{
    grid-area:destination;
    margin-bottom:4rem;
    position: relative;

    ::after{
        position:absolute;
        top:7em;
        left:1%;
        content:"";
        display: block;
        width:17rem;
        opacity:0.1;
        border-bottom:solid 2px;
        box-shadow: 5px 5px 10px #c2c0bc;
    }
}

.details{
    grid-area: detail;
}
.submit-button{
    grid-area:button;
}
.warnings{
    grid-area: warnings;
}

`;


export const CustomSubmitButton = styled.button`
    
    @media (max-width:768px) {
        margin: 2rem 2rem;
    }
    
`;