import { FILL_ROUTES } from './RoutesTypes'

const initState = {  
  routes:[]
}

const RoutesReducer = (state = initState, action) =>{
  switch (action.type) {
    case FILL_ROUTES:
      const routes = action.routes;
      return{
        routes:routes
      }

    default:
      return state;
  }
  
}



export {RoutesReducer};

