import styled from 'styled-components';

const DetailContainer = styled.div`  
    position:relative;
    display:flex;
    flex-direction:column;
    font-weight:700;

    animation:fadeIn 1s;

    color:black;
    padding:1rem;
    margin:2rem 1rem;
    border-radius:20px;

    
    box-shadow: 0px 1px 12px -2px rgba(0,0,0,0.99);

    transition:200ms;
    cursor: pointer;
    :hover{
        background-color:#e38329;
    }
`;

const TopDiv = styled.div`
    display:flex;

.route_icon a img{
    height: 1rem;
    margin:.2rem ;
}

.route_id{
    margin:0rem 1rem ;
}

.route_name{
    white-space:nowrap;
}

.route_location{
    margin-left:.9rem ;
}
`;
const MiddleDiv = styled.div`
    display:flex;

.route_time{
    margin-left:.2rem ;
}
`;
const BottomDiv = styled.div`
    display: flex;

    .route_schedule{
        margin-left:0.2rem;
    }
    .route_price{
        margin-left:0.9rem;
    }
`;

const RouteDeleteButton = styled.a`
    position:absolute;
    right: 1rem;

    display: inline-block;
    transition:500ms;

    :hover{
        transform: scale(1.5);
    }
    
`;
const RouteUpdateButton = styled.a`
    position:absolute;
    right: 1rem;
    top: 3.4rem;

    transition:500ms;
    :hover{
        transform: scale(1.5);
    }
`;

export {RouteUpdateButton,RouteDeleteButton,
        BottomDiv,MiddleDiv,TopDiv,DetailContainer}