import React from 'react'
import { BackDrop,ConfirmationWindow,CustomH3,CustomButton } from './Modal_styled'
import { connect } from 'react-redux';
import { toggleConfirmationMessage, deleteRoute } from '../Redux/ReduxActions';


function Modal(props) {

  const { switchConfirmationMessage,deleteRoute } = props;

  const { confirmationMessage,routeIdToDelete } = props.reducers.GeneralReducer;  
  return (
    <BackDrop style={confirmationMessage===true?{display:'flex'}:{display:'none'}} 
      onClick={()=>{switchConfirmationMessage({confirmationMessage})}}>

      <ConfirmationWindow onClick={(event)=>{
          event.stopPropagation();
        }}>
        <CustomH3 onClick={(event)=>{
          event.stopPropagation();
          
          }}>Seguro que desea Eliminar la ruta?</CustomH3>
        <CustomButton  className=" bg-blue-500" onClick={(event)=>{
          event.stopPropagation();
          switchConfirmationMessage({confirmationMessage})
          deleteRoute(routeIdToDelete)
            
        }}>
          Afirmativo
        </CustomButton>
        <CustomButton  className=" bg-blue-500" onClick={(event)=>{
          event.stopPropagation()
          switchConfirmationMessage({confirmationMessage})
        }}>
          Cancelar
        </CustomButton>
      </ConfirmationWindow>
    </BackDrop>
  )
}

const MapRootStateToProps = (reducers)=>{
    return{
      reducers:reducers
    }
}

const MapDispatchToProps = (dispatch)=>{
  return{
    switchConfirmationMessage: (value)=>{dispatch(toggleConfirmationMessage(value))},
    deleteRoute: routeId=>{dispatch(deleteRoute(routeId))}
  }
}

export default connect(MapRootStateToProps,MapDispatchToProps)(Modal)