import bddHelper from '../../BDD/Request'
import { unloadApp } from '../CombinedActions'
import { savingDataInState } from '../RoutesReducer';

const baseURL = `https://safe-inlet-31030.herokuapp.com/api/route`;


export const getRoutes = ()=>{
  return (dispatch)=>{
    bddHelper.getRequest(baseURL)
    .then((data)=>{
      dispatch ( savingDataInState(data) )
    })
    .catch((err)=>{
      console.log(err)
    })
  }
}

export const postRoute = (route)=>{
  return ((dispatch)=>{
    bddHelper.postRequest(baseURL,route)
      .then((data)=>{ 
        dispatch(unloadApp())
        dispatch(getRoutes())
      }).catch((err)=>{
        console.log(err)
      })
  })
}


export const putRoute = (route)=>{
  return ((dispatch)=>{
      bddHelper.putRequest(baseURL,route)
      .then((res)=>{
        dispatch(unloadApp())
        dispatch(getRoutes())
      })
      .catch((err)=>{
        console.log(err)
      })
  })
}

export const deleteRoute = (routeId)=>{
  return (dispatch)=>{
    bddHelper.deleteRequest(baseURL,routeId)
    .then((data)=>{
      dispatch(getRoutes())
    })
    .catch((err)=>{
      console.log(err)
    })
  }
}



