import React from 'react';
import styled from 'styled-components';
import Cleave from 'cleave.js/react';

import { connect } from 'react-redux'; 

const Container = styled.div`

`;

const inputStyle = "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
const labelStyle = "block text-gray-700 text-lg font-bold mb-2";

function EditableInputForm (props) {


const {isDisabled=false,isStandardInput = true,inputValue = "",refName,labelName,customFunction,
    cleaveOptions,_className,maxValue,placeholder,type='text'} =props;
    
    const { generalState } = props.states.GeneralReducer;
    
    if(isStandardInput===true){
        if(labelName === "ID Ruta"){
            return (
                <Container className={_className}>
                    <label className={labelStyle}>{labelName}</label>
                    <input 
                        style={{padding:".5rem"}}
                        type={type} 
                        name ={refName} 
                        onChange={customFunction} 
                        value={inputValue}
                        className= { inputStyle } 
                        disabled={
                            generalState.postUpdateButton === 'put'
                            ?true
                            :false
                        }
                        placeholder={placeholder}
                    />
                </Container>
            )
        }else{
            return (
                <Container className={_className}>
                    <label className={labelStyle}>{labelName}</label>
                    <input 
                        style={{padding:".5rem"}}
                        type='text' 
                        name ={refName} 
                        onChange={customFunction} 
                        value={inputValue}
                        className= { inputStyle } 
                        disabled={isDisabled}
                        placeholder={placeholder}
                        
                    />

                </Container>
            )
        }
    }else{
        return(
            <Container className={_className}>
                <label className={labelStyle}>{labelName}</label>
                <Cleave 
                    options={cleaveOptions}
                    style={{padding:".5rem"}}
                    type={type} 
                    name ={refName} 
                    onChange={customFunction} 
                    value={inputValue}
                    className= { inputStyle } 
                    disabled={isDisabled}
                    max={maxValue}
                    placeholder={placeholder}
                />
            </Container>
        )
    }
}

const mapRootStateToProps = (states)=>{
    return {
        states:states
    } 
}


export default connect(mapRootStateToProps)(EditableInputForm)