import styled from 'styled-components'
export const CustomFormButton = styled.button`
  display:inline-block;
  width:50%;
  color:${props=>props.theme.generalState.tabs === 'form'
          ?'black':'white'
  }; 

  font-weight:600;
  padding:1rem;
  transition:400ms;
  background-color:${props=>props.theme.generalState.tabs === 'form'
    ?'white':'black'
  };

  :hover{
      -webkit-box-shadow: -2px 9px 36px -8px rgba(0,0,0,0.63);
      -moz-box-shadow: -2px 9px 36px -8px rgba(0,0,0,0.63);
      box-shadow: -2px 9px 36px -8px rgba(0,0,0,0.63);
  }
`;

export const CustomRouteButton =  styled(CustomFormButton)`
  color:${props=>
    props.theme.generalState.tabs === 'form'
    ?'white':'black'}; 
  background-color:${props=>
    props.theme.generalState.tabs==='route'?'white':'black'};
`;

export const TabContainer = styled.div`
  position:absolute;
  width: 100%;
  -webkit-box-sizing: border-box;
  box-sizing:border-box;
`;
