import React from 'react'
import {
  FormInputContainer,
  CustomSubmitButton
} from './Form_styled';

import EditableInputForm from './Components/EditableInputForm/EditableInputForm';
import ButtonGroup from '../ButtonGroup/ButtonGroup';

import {postRoute,putRoute } from '../../../Redux/AsyncActions/index'
import { handleInputChange } from '../../../Redux/RouteReducer/index'
import { getRouteList } from './RouteList'

import { connect } from 'react-redux'

function Form (props){
    const { onRouteChange,addRoute,updateRoute } = props;
    const { route } = props.states.RouteReducer;
    
    const { generalState }  = props.states.GeneralReducer;

  const routeList = getRouteList(route);
  return (
    <FormInputContainer className="formInputContainer"
      onSubmit={(event)=>{
          event.preventDefault(); 
          if(generalState.postUpdateButton === 'post'){
            addRoute(route);
          }else{
            updateRoute(route);
          }
          
      }}
    >
      <ButtonGroup/> 

      {routeList.map((route,index)=>{
          return(     
              <EditableInputForm
                  key={index}
                  isStandardInput={route._isStandardInput}
                  cleaveOptions={route._cleave}
                  refName = {route._refName}
                  inputValue={route.routeStateObject}
                  labelName={route._labelName}
                  customFunction={onRouteChange}
                  _className={route._refName}
                  maxValue={route.max}
                  placeholder={route.placeholder}
                  type={route._type}
              />
          )
      })}

      <CustomSubmitButton 
      className="submit-button bg-blue-500 inline-block  text-white font-bold py-2 px-8 rounded hover:bg-blue-700">
          { generalState.postUpdateButton === 'post'
          ? "Guardar"
          : generalState.postUpdateButton === 'put'
          && "Confirmar" }
      </CustomSubmitButton>  

    </FormInputContainer>
  )
}

const mapRootStateToProps = state=>{
  return {
      states:state
  }
}

const mapDispatchToProps = (dispatch)=>{
  return{
      onRouteChange: (event)=>{dispatch(handleInputChange(event))},
      addRoute: (route)=>{dispatch(postRoute(route))},
      updateRoute: (route)=>{dispatch(putRoute(route))}
  }
}


export default connect(mapRootStateToProps,mapDispatchToProps)(Form)