import * as options   from './GeneralTypes'

export const MouseIn = (route)=>{
  return{
    type: options.MOUSE_IN,
    route:route
  }
}

export const MouseOut = ()=>{
  return {
    type: options.MOUSE_OUT
  }
}

export const onRouteTabClick = ()=>{
  return{
    type:options.ROUTE_TAB_CLICK
  }
}

export const onFormTabClick = ()=>{
  return {
    type:options.FORM_TAB_CLICK
  }
}

export const ChangeGeneralTransportType = (transportType)=>{
  return{
    type:options.CHANGE_TRANSPORT_TYPE,
    transportType:transportType
  }
}


export const changePostToPut = ()=>{
  return{
    type:options.CHANGE_POST_TO_PUT_BUTTON
  }
}

export const changePutToPost = ()=>{
  return{
    type:options.CHANGE_PUT_TO_POST_BUTTON
  }
}

export const resetGeneralSettings = ()=>{
  return{
    type:options.RESET_GENERAL_SETTINGS
  }
}


export const markerReferenceMouseIn = (coords)=>{
  return{
    type:options.MARKER_REFERENCE_MOUSE_IN,
    coords:coords
  }
}

export const markerReferenceMouseOut = ()=>{
  return {
    type:options.MARKER_REFERENCE_MOUSE_OUT,
  }
}

export const toggleConfirmationMessage = (payload)=>{
  return{
    type:options.CHANGE_CONFIRMATION_MESSAGE,
    payload:payload
  }
}