import styled from 'styled-components';


const InputContainer = styled.div`
    display:inline-block;
    flex-direction:row;
    position:relative;
    margin:0.5rem;

  
`;

const CustomLabel = styled.label`
`;

const CustomButton = styled.button`
    display: inline-block;
    width: 5rem;
    text-align:center;

    border-radius:20px;
    background-color:#e38329;

    transition:500ms;
    :hover{
      background-color:black;
    }

 
`;
const CustomDeleteButton = styled.button`
  position:absolute;
  z-index:5;
  right: .6rem;
 

  top: 0;  
  transition:500ms;
  color:black;
  display:inline-block;
  width: 1.5rem;
  border-radius:50px;

`;

export {CustomButton,InputContainer,CustomLabel,CustomDeleteButton}