import * as options from './PolylineTypes'

export const addingPolyline = (coords)=>{
  return{
    type:options.ADD_POLYLINE,
    coords:coords
  }
}


export const updatingPolyline = (refData)=>{
  return {
    type: options.UPDATE_POLYLINE,
    refData:refData
  }
}

export const DeletePolyline = (event)=>{
  return {
    type:options.DELETE_POLYLINE,
    event:event
  }
}


export const fillingPolyline = (currentRoute)=>{
  return {
    type: options.FILL_POLYLINE,
    currentRoute:currentRoute
  }
}


export const unloadPolyline = ()=>{
  return {
    type: options.UNLOAD_POLYLINE
  }
}