import styled from 'styled-components';

const Container = styled.div`
    grid-area:form;
    margin-top:6rem;
    

    display:grid;
    grid-template-columns:1fr;
    grid-template-rows:1fr 6fr;
    grid-template-areas:
    'r_detail'
    'r_detail';

    
    .routeTitle{
        grid-area:r_title;
        font-size:1rem;
        text-align:left;
        padding:0.5rem;
        margin-left:1rem;
        color:white;
    }

    .routeDetail{
        grid-area:r_detail;
        
    }
`;

const RoutesContainer = styled.div`
    color: white;
    overflow:auto;

`;


export {RoutesContainer,Container}