import * as options from './PolylineTypes'

const initState = {
  options:{
    strokeColor: '#EF6C00',
    strokeOpacity: 1,
    strokeWeight: 5,
      icons: [{ 
        icon: "hello",
        offset: '0',
        repeat: '3px'
      }]
  },
  polylineCoordinates : [ ]
}



const PolylineReducer = (state = initState, action) =>{
  switch (action.type) {
    case options.UPDATE_POLYLINE:
      const { lat:updated_lat,lng:updated_lng,
            currentPosition:{lat:current_lat,lng:current_lng}} = action.refData;
      
      return {
        ...state,
        polylineCoordinates:state.polylineCoordinates.map((polyline,index)=>{  
          if(current_lat === polyline.lat){
            if(current_lng === polyline.lng){
              return {
                lat:updated_lat,lng:updated_lng
              }
            }else{
              return polyline
            }   
          }else{
            return polyline
          }
        })
      }
    case options.UNLOAD_POLYLINE:
      return{
        ...state,
        polylineCoordinates:[]
      }
    case options.FILL_POLYLINE:
      
      const { position } = action.currentRoute
      

      return {
        ...state,
        polylineCoordinates:position.map((routeFromChild,index)=>{
          const [lat,lng] = routeFromChild.LatLng; 
            return {
              lat:lat,lng:lng 
            };
        })
      };
    case options.ADD_POLYLINE:
      const { lat,lng } = action.coords;
      return{
        ...state,
        polylineCoordinates : [
          ...state.polylineCoordinates,
          { lat:lat,lng:lng }
        ]
      }
    case options.DELETE_POLYLINE:
      const coordinatesToDelete = action.event.target.elements[0].value;
      return{
        ...state,
        polylineCoordinates : [
          ...state.polylineCoordinates.filter(
            function (coordinates,index){
              // eslint-disable-next-line
              if(coordinatesToDelete == coordinates.lat){  
                return !{coordinates}
              }else{
                return {coordinates}
              }      
            }
          )
        ]
      }
    default:
      return state;
  }
  
}

export {PolylineReducer};