
export const getRouteList = (routeState)=>{
  const routeList = [
    {
        _labelName:'ID Ruta',_refName:'ID',
        routeStateObject:routeState.ID,
        max:10,
        placeholder:"115B"
    },
    {
        _labelName:'Nombre Ruta',_refName:'routeName',
        routeStateObject:routeState.routeName,
        max:30,
        placeholder:"Charles 9"
    },
    {
        _labelName:'Precio',_refName:'price',
        routeStateObject:routeState.price,
        _isStandardInput:false,
        _cleave:{numeral: true,numeralThousandsGroupStyle: 'thousand',blocks:[4,2,4]},
        max:400,
        placeholder:"$25",
        
    },
    {
        _labelName:'Intervalo',_refName:'interval',
        routeStateObject:routeState.schedule.interval,
        _type:'number',
        _cleave:{time: true, timePattern: ['h', 'm']},
        placeholder:"00:15"
    },
    {
        _labelName:'Destino',_refName:'destination',
        routeStateObject:routeState.destination,
        max:30,
    },
    {
        _labelName:'Hora Ini',_refName:'startTime',
        routeStateObject:routeState.schedule.startTime,
        _type:'time',
        _isStandardInput:false,
    },
    {
        _labelName:'Hora Fin',_refName:'endTime',
        routeStateObject:routeState.schedule.endTime,
        _isStandardInput:false,
        _cleave:{time: true, timePattern: ['h', 'm']},
        _type:'time'
    },
    {
        _labelName:'Detalles',_refName:'details',
        routeStateObject:routeState.aditionalInfo.details,
        max:50,
    },
    {
        _labelName:'Advertencia',_refName:'warnings',
        routeStateObject:routeState.aditionalInfo.warnings,
        max:100,
    }
  ]
  return routeList
}